.card {
  transition: box-shadow 0.3s ease; /*permite indicarnos que propiedades de css queremos que sean animadas*/
  display: flex;
  width: 300px;
  margin: 10px 70px;
  /* border: 3px solid #000; */
  border-radius: 20px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0.5);
  cursor: pointer;
}

.card:hover {
  box-shadow: 0 0.1rem 0.4rem rgba(0, 0, 0, 0);
}

img {
  width: 6rem;
  padding: 5px;
}
